import 'bootstrap/dist/css/bootstrap.css'
import { Navbar } from 'react-bootstrap';
import './navbar.css'
import InstagramLogo from '../../img/Instagram_logo.png';
import GithubLogo from '../../img/GitHub_Logo.png';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom';

import Hannes from '../Hannes/Hannes'
import RubiksCube from '../RubiksCube/RubiksCube'

function NavbarOwn() {

    return(
        <div className="navbar-background">



            
            <Navbar className="navbar navbar-expand-lg navbar-dark bg-dark">
                <a className="navbar-brand navbar-margin" href="#">iAmWebLuk</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse"
                        data-target="#navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <div className="navbar-toggler-inner"></div>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav">
                        <li className="nav-item active">
                            <a className="nav-link" href="#biography">Biography</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="rubiksCube.html">Rubiks Cube</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="CV.pdf" target="_blank">Get my CV</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="javascriptIdeas.html">JS Test</a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="react_exercise.html">React Test</a>
                        </li>
                        <li style={{color: 'white'}}>
                            Rubiks Cube
                        </li>
                    </ul>


                    <Router>
                        <Navbar />
                        <Route path='/rubikscube' component={ RubiksCube } />
                    </Router>
                    <ul className="navbar-nav flex-row flex-wrap ms-md-auto">
                        <li className="nav-item">
                            <a href="https://www.instagram.com/lukas.weber_/"><img src={ InstagramLogo } width="40"
                                                                                   height="40" className="logo-Margin" 
                                                                                    /></a>
                            <a href="https://github.com/iamWebLuk"><img src={ GithubLogo } alt="" className='logo-Margin'/></a>
                        </li>
                    </ul>
                </div>
            </Navbar>
        </div>
    );
}

export default NavbarOwn;