import passFoto from '../../img/Passfoto.png'
import './biography.css'


function Biography() {

  return (
    <div className="header">
      <div className="content">
          <div className="header">
            <div className="content">
              <section id="biography">
<br /><br /><br />
                <div className="container">
                  <div className="row">
                    <div className="col-sm" style={{textAlign: 'center'}}>
                      <img src={ passFoto } alt="Photo" />
                     
                    </div> 
                     <div className="col-sm  column-margin" style={{fontFamily: 'Brygada 1918'}}>

                      <h1 style={{color: 'black'}}>Biography</h1>
                      <hr />
                        <div style={{fontSize: '18px'}}>
                        My name is Lukas, I am student at the University of
                            Applied Sciene FH Joanneum.
                        
                        I was born and raised in lower austria and since september
                            2019 I live in Graz for
                            my studies. My major is Mobile Software Development.
                            Currently I am in the 3rd semester but about to get into
                            the 4rd semester.
                            At this website you can see my hobbys and what I like.
                        </div>
                    </div>
                  </div>
                  </div>
                  <br />
                </section>
                </div>
                </div>
                </div>
                </div>
                ); 
                }

export default Biography;